import firebase from "gatsby-plugin-firebase"
import React, {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
} from "react"
import { copyToClipboard } from "../utils/copyToClipboard"
import {
  sortDigitsTangrams,
  sortLettersTangrams,
  sortTangrams,
} from "../utils/sortTangrams"
import { useTranslate } from "./language"
import { NotifyContext } from "./notify"
import { TangramsContext } from "./tangrams"
import { UserContext } from "./user"
import { shuffle } from "../utils/shuffle"

export const GalleryContext = createContext(null)

export const GalleryProvider = ({ children }) => {
  const t = useTranslate()
  const notify = useContext(NotifyContext)
  const { currentUser } = useContext(UserContext)
  const tangrams = useContext(TangramsContext)

  const [playlist, setPlaylist] = useState(null)
  const [saveRequestId, setSaveRequestId] = useState(0)
  const [completedTangrams, setCompletedTangrams] = useState(null)
  const [tangramsByCategory, setTangramsByCategory] = useState(null)

  const [initialized, setInitialized] = useState(false)

  useEffect(() => {
    if (tangramsByCategory && completedTangrams) {
      setInitialized(true)
    }
  }, [tangramsByCategory, completedTangrams, playlist])

  useEffect(() => {
    const unsubscribe = firebase
      .firestore()
      .collection("stats")
      .onSnapshot((collectionSnapshot) => {
        const newCompletedTangramsByUserId = {}
        for (const doc of collectionSnapshot.docs) {
          const id = doc.id
          const completionStats = doc.data()
          newCompletedTangramsByUserId[id] = completionStats
        }
        setCompletedTangrams(newCompletedTangramsByUserId)
      })

    return unsubscribe
  }, [currentUser])

  useEffect(() => {
    if (tangrams === null) {
      return
    }

    const newTangramsByCategory = {}

    for (const tangram of tangrams) {
      if (tangram.uid && !tangram.approved) {
        if (
          !currentUser ||
          (!currentUser.isAdmin && tangram.uid !== currentUser.uid)
        ) {
          continue
        }
      }

      if (!newTangramsByCategory[tangram.category]) {
        newTangramsByCategory[tangram.category] = []
      }

      newTangramsByCategory[tangram.category].push(tangram)
    }

    const sortedTangramsByCategory = {}

    for (const sortedCategory of Object.keys(newTangramsByCategory).sort()) {
      sortedTangramsByCategory[sortedCategory] = newTangramsByCategory[
        sortedCategory
      ].sort(
        sortedCategory === "letters"
          ? sortLettersTangrams
          : sortedCategory === "digits"
          ? sortDigitsTangrams
          : sortTangrams
      )
    }

    setTangramsByCategory(sortedTangramsByCategory)
  }, [tangrams, currentUser])

  const shareTangrams = useCallback(
    (tangrams) => {
      let challengeLink = `${window.location.origin}/?tangrams=${tangrams
        .map(({ id }) => id)
        .join(",")}`
      if (currentUser) {
        challengeLink += `&uid=${currentUser.uid}`
      }
      copyToClipboard(challengeLink)
      notify(t("Challenge link copied to clipboard"))
    },
    [notify, t, currentUser]
  )

  const requestSave = useCallback(() => {
    setSaveRequestId((prevRequestId) => prevRequestId + 1)
  }, [])

  const markTangramAsComplete = useCallback(
    async (tangram, completionTime) => {
      if (currentUser && !completedTangrams[tangram.id]) {
        await firebase
          .firestore()
          .collection("stats")
          .doc(currentUser.uid)
          .set(
            {
              [tangram.id]: { completionTime },
            },
            { merge: true }
          )
      }
    },
    [currentUser, completedTangrams]
  )

  const startRandomPlaylist = useCallback(() => {
    setPlaylist(
      shuffle(tangrams.filter(({ approved }) => approved)).sort(
        ({ id: idA, edges: edgesA }, { id: idB, edges: edgesB }) => {
          const isTangramACompleted =
            currentUser != null &&
            completedTangrams[currentUser.uid] !== undefined &&
            completedTangrams[idA] !== undefined
          const isTangramBCompleted =
            currentUser != null &&
            completedTangrams[currentUser.uid] !== undefined &&
            completedTangrams[idB] !== undefined
          return isTangramACompleted === isTangramBCompleted
            ? edgesB - edgesA
            : isTangramACompleted - isTangramBCompleted
        }
      )
    )
  }, [tangrams, completedTangrams, currentUser])

  const contextValue = useMemo(
    () => ({
      initialized,
      requestSave,
      saveRequestId,
      playlist,
      setPlaylist,
      tangramsByCategory,
      markTangramAsComplete,
      completedTangrams,
      shareTangrams,
      startRandomPlaylist,
    }),
    [
      initialized,
      requestSave,
      saveRequestId,
      playlist,
      tangramsByCategory,
      markTangramAsComplete,
      completedTangrams,
      shareTangrams,
      startRandomPlaylist,
    ]
  )

  return (
    <GalleryContext.Provider value={contextValue}>
      {children}
    </GalleryContext.Provider>
  )
}
